<template>
  <nav>
    <ul class="categoryNav">
      <p>
        <br /><br />
        Browse By Category
      </p>
      <template v-for="category in categories">
        <li
          :key="category.categoryId"
          v-if="selectedCategoryName === category.name"
          class="selected"
          tag="li"
        >
          {{ category.name }}
        </li>
        <li :key="category.categoryId" v-else>
          <router-link
            :to="{ name: 'category', params: { name: category.name } }"
            tag="li"
          >
            {{ category.name }}
          </router-link>
        </li>
      </template>
    </ul>
  </nav>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "CategoryNav",
  computed: {
    ...mapState(["categories", "selectedCategoryName"])
  }
};
</script>

