  margin-top: 1em;
}

form {
  display: flex;
  flex-direction: column;
}
form > div {
  display: flex;
  justify-content: space-between;
}
form > div > input,
form > div > select {
  margin-left: 0.5em;
}

form > div > label {
  margin-top: 0.3em;
  align-content: start;
}
form > .error {
  color: red;
  text-align: right;
  margin-bottom: 1px;
}

.checkoutStatusBox {
  margin: 1em;
  padding: 1em;
  text-align: center;
}
