<template>
  <li class="bookBlock">
    <div class="bookImage">
      <router-link to="/category">
        <img
          :src="require('@/assets/images/books/' + bookImageFileName(book))"
          :alt="book.title"
        />
      </router-link>
    </div>
    <ul class="bookInfo">
      <li class="bookTitle">{{ book.title }}</li>
      <li class="bookAuthor">{{ book.author }}</li>
      <li class="bookPrice">${{ (book.price / 100).toFixed(2) }}</li>
    </ul>
    <div class="addToCarButton">
      <a class="bookButtonLink" @click="addToCart(book)" href="#"
        >Add to Cart</a
      >
    </div>
    <div v-if="book.isPublic" class="readNowButton">
      <a class="bookButtonLink" href="#">Read Now</a>
    </div>
  </li>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "categoryBookListItem",
  props: {
    book: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...mapState(["cart"])
  },
  methods: {
    bookImageFileName: function(book) {
      let name = book.title.toLowerCase();
      name = name.replace(/ /g, "-");
      return `${name}.png`;
    },
    addToCart: function(book) {
      this.$store.dispatch("addToCart", book);
    }
  }
};
</script>
