<template>
  <div class="categoryContent">
    <category-nav></category-nav>
    <category-book-list :books="selectedCategoryBooks"> </category-book-list>
  </div>
</template>

<script>
import CategoryNav from "@/components/CategoryNav";
import CategoryBookList from "@/components/CategoryBookList";
import { mapActions } from "vuex";
import { mapState } from "vuex";
export default {
  name: "category",
  components: {
    CategoryNav,
    CategoryBookList
  },
  computed: {
    ...mapState(["selectedCategoryBooks"])
  },
  methods: {
    ...mapActions(["selectCategory"])
  },
  created: function() {
    const vm = this;
    this.selectCategory(this.$route.params.name);
    this.$store.dispatch("fetchSelectedCategoryBooks").catch(function() {
      vm.$router.push("/404");
    });
  }
};
</script>

<style scoped>
.categoryContent {
  display: flex;
  justify-content: space-around;
}
</style>
