<template>
  <section v-if="!cart.empty">
    <table>
      <tr>
        <th>Book Details</th>
        <th>Price</th>
        <th>Quantity</th>
        <th>Total</th>
      </tr>
      <tr v-for="item in cart.items" :key="item.bookId">
        <td>
          <img
            class="book-image"
            :alt="item.book.title"
            :src="
              require('@/assets/images/books/' + bookImageFileName(item.book))
            "
          />
          {{ item.book.title }}
          <p id="bookAuthor">By {{ item.book.author }}</p>
        </td>
        <td>
          <Price :amount="item.book.price" />
        </td>
        <td>
          <div>
            {{ item.quantity }}
            <section>
              <img
                @click="
                  updateCart({ book: item.book, quantity: item.quantity + 1 })
                "
                src="../assets/images/site/cr-increment-icon.png"
                alt="Increment quantity"
              />
            </section>
            <section>
              <img
                @click="
                  updateCart({ book: item.book, quantity: item.quantity - 1 })
                "
                src="../assets/images/site/cr-decrement-icon.png"
                alt="Decrement quantity"
              />
            </section>
          </div>
        </td>
        <td>
          <Price :amount="item.book.price * item.quantity"></Price>
        </td>
      </tr>
      <tr>
        <td></td>
        <td></td>
        <td>Cart Subtotal:</td>
        <td>
          <Price :amount="cart.subtotal" />
        </td>
      </tr>

      <tr />
      <tr>
        <td></td>
        <td></td>
        <td>Shipping Cost:</td>
        <td>$5.00</td>
      </tr>

      <tr />
      <tr>
        <td></td>
        <td></td>
        <td>Cart Total:</td>
        <td>
          <b>
            <Price :amount="cart.total" />
          </b>
        </td>
      </tr>

      <tr />
    </table>
  </section>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Price from "@/components/Price";

export default {
  name: "CartTable",
  components: {
    Price
  },
  computed: {
    ...mapState(["cart"])
  },
  methods: {
    ...mapActions(["updateCart"]),
    bookImageFileName: function(book) {
      let name = book.title.toLowerCase();
      name = name.replace(/ /g, "-");
      name = name.replace(/ /g, "-");
      name = name.replace(/ /g, "-");
      name = name.replace(/ /g, "-");
      name = name.replace(/ /g, "-");
      return `${name}.png`;
    }
  }
};
</script>

