<template>
  <div id="categoryDropdown">
    <button class="button--primary">Shop By Category &nabla;</button>

    <ul>
      <template v-for="category in categories">
        <li
          class="selected"
          :key="category.categoryId"
          v-if="selectedCategoryName === category.name"
        >
          {{ category.name }}
        </li>
        <li :key="category.categoryId" v-else>
          <router-link
            :to="{ name: 'category', params: { name: category.name } }"
            tag="li"
          >
            {{ category.name }}
          </router-link>
        </li>
      </template>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "HeaderCategoryDropdown",
  computed: {
    ...mapState(["categories", "selectedCategoryName"])
  }
};
</script>

